#buttonText:hover::after {
  content: "Contact Me";
} */
</style>

<template>
  <div>
    <v-container fluid>
      <v-row align="center" style="height: 300px; background-color: black;">
        <v-row justify="center">
          <v-dialog v-model="dialog" persistent max-width="600px">
            <template v-slot:activator="{ on }">
              <v-btn
                color="grey"
                id="buttonText"
                dark
                v-on="on"
                data-aos="zoom-in"
                data-aos-duration="1800"
              >Free Memes</v-btn>
            </template>
            <v-card>
              <v-card-title>
                <span class="headline">Contact Us</span>
                <a href="https://www.linkedin.com/in/adamsbala/">
                  <img src="https://img.icons8.com/material-outlined/50/000000/linkedin.png">
                </a>
              </v-card-title>
              <v-divider></v-divider>
              <v-card-text>
                <v-container>
                  <v-row>
                    <!-- <v-col cols="12">
                      <v-text-field
                        label="Your Email*"
                        required
                        v-model="Email"
                        :rules="emailRules"
                      ></v-text-field>
                    </v-col> -->

                    <v-col cols="12">
                      <v-text-field label="Your Name*" required v-model="Name"></v-text-field>
                    </v-col>

                    <v-col cols="12">
                      <v-textarea
                        label="Message*"
                        outlined="true"
                        type="text"
                        required
                        v-model="Message"
                      ></v-textarea>
                    </v-col>
                  </v-row>
                </v-container>
                <small>*indicates required field</small>
              </v-card-text>
              <v-card-actions>
                <div class="flex-grow-1"></div>
                <v-btn color="blue darken-1" text @click="dialog = false">Close</v-btn>
                <v-btn
                  color="blue darken-1"
                  text
                  @click="dialog = false"
                  v-on:click="sendEmail"
                >Send Email</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-row>
      </v-row>
    </v-container>
  </div>
</template>

<script>

export default {
  Email: "",
  Name: "",
  Message: "",

  data() {
    return {
      emailRules: [
        v => !!v || "E-mail is required",
        v => /.+@.+\..+/.test(v) || "E-mail must be valid"
      ],
      dialog: false
    };
  },
  methods: {
    sendEmail: function() {
     window.open('mailto:hireadams19@gmail.com?subject=' + this.Name +'&body=' + this.Message);
      alert('Lol uhh this program is not yet that finished so please wait for a bit... I rushed the development Thank you for contacting us tho..we will get back to you soon');
    }
  }
};
</script>