<template>
  <div id="app" style="min-height: 500px;">
    <v-row class="mb-6">
      <v-col>
        <v-card :elevation="20">
          <v-row justify="center">
            <v-card-title>Finish the sentence</v-card-title>
          </v-row>
           <v-divider></v-divider>
          <v-row>
            <v-col :sm="12" :offset-md="1" :md="6">
              <graph-bar
                :width="600"
                :height="400"
                :axis-min="0"
                :axis-max="50"
                :labels="words"
                :values="wordValues"
              >
                <note :text="'Most Common Words Used to Describe Adams'"></note>
                <tooltip :names="words" :position="'left'"></tooltip>
                <legends :names="words"></legends>
              </graph-bar>
            </v-col>
            <v-col :sm="12" :md="5" data-aos ="zoom-in" data-aos-duration="2800">
              <v-row>
                <v-text class="font-weight-black" style="font-size: 40px;"> I love Adams because he is ...</v-text>
              </v-row>
              <v-row align="center">
                <v-col cols="12" sm="6">
                  <div v-for="(item, i) in words" :key="i">
                    <div class="my-2">
                      <v-btn x-large
                            color="success" >{{item}}</v-btn>
                    </div>
                  </div>
                </v-col>
              </v-row>
            </v-col>
          </v-row>
        </v-card>
      </v-col>
    </v-row>
    <!-- <div 
            v-for="(posts, i) in msg[0].data"> 
            <p>{{posts.words}}</p>
    </div>-->
  </div>
</template>
<script>
import PostService from "../postService";

export default {
  data: () => ({
    msg: [],
    error: "",
    words: [],
    wordValues: []
  }),
  async created() {
    try {
      this.msg = await PostService.getPosts();
      const entries = Object.entries(this.msg[0].data.words);
      for (const [words, values] of entries) {
        this.words.push(words);
        this.wordValues.push(values);
      }
    } catch (err) {
      this.error = err.message;
    }
  }
};
</script>