import axios from 'axios';

const url = 'http://localhost:8080/register'

class PostService {
    //Get Stuff
    static getPosts() {
        return new Promise( async (resolve, reject) => {
            try {
                const res = await axios.get(url);
                const data = res.data;
                resolve(
                    data.map(post => ({
                        ...post
                    }))
                )
            } catch(err){
                reject(err)
            }
        })
    }

    //Post Stuff
}

export default PostService;



